.blog-slider {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .blog-slide {
    text-align: center;
    background-color: #fff;
  }
  
  .blog-title {
    color: #000;
  }

  .slick-dots li {
    border: 1px solid #000;
    border-radius: 2rem;
  }

  .slick-dots .slick-active {
    border: 1px solid #000;
    background-color: #000;
  }
  
  /* Additional styles can be added as needed */
  